<template>
  <v-card>
    <v-toolbar class="elevation-0">
      <v-btn @click="addItem" color="primary"></v-btn>
      <v-spacer/>
      <v-flex xs3>
        
        <v-btn-toggle mandatory v-model="search.saleable">
          <v-btn flat>
            
          </v-btn>
          <v-btn flat :value="true">
            
          </v-btn>
          <v-btn flat :value="false">
            
          </v-btn>
        </v-btn-toggle>
      </v-flex>
      <v-flex xs3>
        <v-text-field
          append-icon="search"
          label=""
          single-line
          hide-details
          v-model="search.key"
        />
      </v-flex>
    </v-toolbar>
    <v-divider/>
    <v-data-table
      :headers="headers"
      :items="items"
      :pagination.sync="pagination"
      :total-items="totalItems"
      :loading="loading"
      class="elevation-1"
    >
      <template slot="items" slot-scope="props">
        <td class="text-xs-center">{{ props.item.id }}</td>
        <td class="text-xs-center">{{ props.item.title }}</td>
        <td class="text-xs-center">{{ props.item.categoryName}}</td>
        <td class="text-xs-center">{{ props.item.brandName }}</td>
        <td class="justify-center layout px-0">
          <v-btn icon small @click="editItem(props.item)">
            <i class="el-icon-edit"/>
          </v-btn>
          <v-btn icon small @click="deleteItem(props.item.id)">
            <i class="el-icon-delete"/>
          </v-btn>
          <v-btn icon small v-if="props.item.saleable"></v-btn>
          <v-btn icon v-else></v-btn>
        </td>
      </template>
      <template slot="no-data">
        <v-alert :value="true" color="error" icon="warning">
           :(
        </v-alert>
      </template>
      <template slot="pageText" slot-scope="props">
        {{props.itemsLength}},:{{ props.pageStart }} - {{ props.pageStop }}
      </template>
    </v-data-table>

    <v-dialog v-if="show" v-model="show" max-width="900" scrollable persistent>
      <v-card>
        <v-toolbar dense dark color="primary">
          <v-toolbar-title>{{isEdit ? '' : ''}}</v-toolbar-title>
          <v-spacer/>
          <v-btn icon @click="closeForm">
            <v-icon>close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text style="height: 600px;">
          <!--  -->
          <goods-form :oldGoods="selectedGoods" :isEdit="isEdit" :step="step" ref="goodsForm" @closeForm="closeForm"/>
        </v-card-text>
        <v-card-actions>
          <v-layout row justify-center>
            <v-flex xs2>
              <v-btn :disabled="step === 1" @click="lastStep"></v-btn>
            </v-flex>
            <v-flex xs2>
              <v-btn :disabled="step === 4" color="primary" @click="nextStep"></v-btn>
            </v-flex>
          </v-layout>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
  import GoodsForm from './GoodsForm'
  import {goodsData} from "../../mockDB";

  export default {
    name: "item",
    components: {
      GoodsForm
    },
    data() {
      return {
        search: {
          key: '',
          saleable: true,
        },// 
        totalItems: 0,// 
        items: [],// 
        loading: true,
        pagination: {},// 
        headers: [// 
          {text: 'id', align: 'center', value: 'id'},
          {text: '', align: 'center', sortable: false, value: 'name'},
          {text: '', align: 'center', sortable: false, value: 'image'},
          {text: '', align: 'center', value: 'letter', sortable: true,},
          {text: '', align: 'center', value: 'id', sortable: false}
        ],
        show: false,// 
        selectedGoods: null, // 
        isEdit: false, // 
        step: 1// 
      }
    },
    watch: {
      pagination: {
        handler() {
          this.getDataFromApi();
        },
        deep: true
      },
      search: {
        handler() {
          this.getDataFromApi();
        },
        deep: true
      }
    },
    mounted() {
      this.getDataFromApi();
    },
    methods: {
      closeForm() {
        this.isEdit = false;
        this.show = false;
        this.step = 1;
        this.selectedGoods = null;
        this.getDataFromApi();
      },
      lastStep() {
        if (this.step === 1) {
          return;
        }
        this.step--;
      },
      nextStep() {
        if (this.step === 4) {
          return;
        }
        if (this.$refs.goodsForm.$refs.basic.validate()) {
          this.step++;
        }
      },
      addItem() {
        this.selectedGoods = null;
        this.isEdit = false;
        this.show = true;
      },
      editItem(item) {
        this.selectedGoods = item;
        const names = item.categoryName.split("/");
        this.selectedGoods.categories = [
          {id: item.cid1, name: names[0]},
          {id: item.cid2, name: names[1]},
          {id: item.cid3, name: names[2]}
        ];
        // 
        this.$http.get("/item/goods/spu/detail/" + item.id)
          .then(resp => {
            this.selectedGoods.spuDetail = resp.data;
            this.selectedGoods.spuDetail.specTemplate = JSON.parse(resp.data.specTemplate);
            this.selectedGoods.spuDetail.specifications = JSON.parse(resp.data.specifications);
          })
        this.isEdit = true;
        this.show = true;
      },
      deleteItem(id) {
        this.$message.confirm(', ?')
          .then(() => {
            // 
            this.$http.delete("/item/goods?id=" + id)
              .then(() => {
                // 
                this.getDataFromApi();
                this.$message.info('!');
              })
          })
          .catch(() => {
            this.$message.info('');
          });

      },
      getDataFromApi() {
        this.loading = true;
        setTimeout(() => {
          // 
          this.items = goodsData.slice(0, 4);
          this.totalItems = 25;
          this.loading = false;
        }, 300)
      }
    }
  }
</script>

<style scoped>
  label {
    font-size: 14px;
  }
</style>



